* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

/* Other styles */
p,
h1 {
  margin: 0;
}

ul {
  padding: 0;
  margin: 0;

  list-style-type: none;
}

.country-list {
  display: flex;
  flex-direction: column;
  gap: 4px;
}

.country-item {
  display: flex;
  align-items: center;
  gap: 8px;
}

.country-name {
  font-size: 18px;
}

.country-info {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.country {
  display: flex;
  align-items: center;
  gap: 8px;
}

.country .country-name {
  font-size: 28px;
}

.country-capital {
  font-size: 18px;
}

.country-capital-bold {
  font-weight: 700;
}
